import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'mapObjectPie',
    pure: true
})

export class MapObjectPie implements PipeTransform {

    transform(value: any, args: any[], mapKey: string): any {
        if (args){
            for (const i in args){
                if (value === args[i][mapKey]){
                    return args[i];
                }
            }
            return null;
        }
    }
}
