
import { MessageService } from 'primeng/components/common/messageservice';
import { TimeoutError } from 'rxjs/Rx';

export function formatDate(theDate, format) {
  if (Object.prototype.toString.call(theDate) === '[object String]' || typeof theDate === 'number') {
    theDate = new Date(theDate)
  }
  const date = {
    'M+': theDate.getMonth() + 1,
    'd+': theDate.getDate(),
    'h+': theDate.getHours(),
    'm+': theDate.getMinutes(),
    's+': theDate.getSeconds(),
    'q+': Math.floor((theDate.getMonth() + 3) / 3),
    'S+': theDate.getMilliseconds()
  };
  if (/(y+)/i.test(format)) {
    // tslint:disable-next-line:no-parameter-reassignment
    format = format.replace(RegExp.$1, (theDate.getFullYear() + '').substr(4 - RegExp.$1.length));
  }
  for (const k in date) {
    if (new RegExp('(' + k + ')').test(format)) {
      // tslint:disable-next-line:no-parameter-reassignment
      format = format.replace(RegExp.$1, RegExp.$1.length === 1
        ? date[k] : ('00' + date[k]).substr(('' + date[k]).length));
    }
  }
  return format;
}


export function findObject(value: any, args: any[], mapKey: string, mapKey2?: string): any {
  if (args) {
    for (const i in args) {
      if (mapKey2) {
        if (value === args[i][mapKey][mapKey2]) {
          return args[i][mapKey];
        }
      } else {
        if (value === args[i][mapKey]) {
          return args[i];
        }
      }
    }
    return null;
  }
}

export function conversionExtToNgTree(treeNodes: any, arr: any[]): any {
  if (!treeNodes || !treeNodes.length) { return; }
  for (let i = 0, len = treeNodes.length; i < len; i++) {
    const childs = treeNodes[i].children;
    delete treeNodes[i].children;
    const level = treeNodes[i];
    arr[i] = {
      data: level,
      children: childs
    };
    if (childs && childs.length > 0) {
      arr[i].children = [];
      conversionExtToNgTree(childs, arr[i].children);
    }
  }
  return arr;
}

export function removeByValue(arr, val) {
  for (let i = 0; i < arr.length; i++) {
    if (arr[i] === val) {
      arr.splice(i, 1);
      break;
    }
  }
}


export function getLeafTree(data, attr1, attr2) {
  if (data.length === 0) {
    return;
  } else {
    // tslint:disable-next-line:prefer-for-of
    for (let i = 0; i < data.length; i++) {
      data[i][attr1] = ['1'];
      data[i][attr2] = ['1'];
      if (data[i].children) {
        getLeafTree(data[i].children, attr1, attr2);
      }
    }
  }
}


export function handleResult(data: any, messageService: MessageService, callBack: Function, op: boolean = false, msgObj: any = null): void {
  if (data.returnCode.type === 'S') {
    if (op) {
      if (msgObj.message) {
        messageService.clear();
        messageService.add({ severity: 'success', summary: '信息提示框', detail: (msgObj && msgObj.message) || data.returnCode.message });
      }
    }
    callBack();
  } else {
    messageService.add({ severity: 'error', summary: '信息提示框', detail: data.message || data.returnCode.message || (msgObj && msgObj.errorMsg) });
  }
}

