export function handleError(err: any, messageService: MessageService, callBack: Function, msgObj: any = null): void {
  messageService.clear();
  if (err.status === 0) {
    messageService.add({ severity: 'error', summary: '信息提示框', detail: '网络连接错误' });
  } else if (err.status === 404) {
    messageService.add({ severity: 'error', summary: '信息提示框', detail: '请求不存在' });
  } else if (err.status === 500) {
    messageService.add({ severity: 'error', summary: '信息提示框', detail: '系统错误' });
  } else {
    if (err instanceof TimeoutError) {
      messageService.add({ severity: 'error', summary: '信息提示框', detail: '请求超时' });
    } else {
      messageService.add({ severity: 'error', summary: '信息提示框', detail: (msgObj && msgObj.errorMsg) || '未知错误' });
    }
  }
  callBack();
}


export function handleErrorImportRule(err: any, messageService: MessageService, callBack: Function, msgObj: any = null): void {
  messageService.clear();
  if (err.status === 0) {
    messageService.add(
      { severity: 'error', summary: '信息提示框', detail: `网络连接错误、状态码${err.status}` });
  } else if (err.status === 404) {
    messageService.add({ severity: 'error', summary: '信息提示框', detail: `请求不存在、状态码${err.status}` });
  } else if (err.status === 500) {
    const returnMes = JSON.parse(err._body);
    messageService.add({ severity: 'error', summary: '信息提示框', detail: `${returnMes.message}` });
  } else {
    if (err instanceof TimeoutError) {
      messageService.add({ severity: 'error', summary: '信息提示框', detail: `请求超时` });
    } else {
      messageService.add({ severity: 'error', summary: '信息提示框', detail: (msgObj && msgObj.errorMsg) || '未知错误' });
    }
  }
  callBack();
}


export function ArrayToJson(arr) {
  const strMap = {};
  for (const v of arr) {
    strMap[v] = null;
  }
  return strMap;
}


/*
* 参数 arr 为获取的新数据数组
*     arr2 为提交数据前的数组
*
*
* */
export function expandLastNode(arr, arr2) {
  // tslint:disable-next-line:forin
  for (const index in arr) {
    if (arr2[index] && arr2[index]['expanded'] !== 'undefined') {
      arr[index].expanded = arr2[index].expanded;
    }

    if (arr[index].children && arr[index].children.length > 0) {
      expandLastNode(arr[index].children, arr2[index].children);
    }
  }
}



/*

 功能:将树的stringName属性全部收集
 obj: 对象
 stringName：属性名
 arr2：结果数组
 */
export function collectAllSystemIds(obj, stringName, arr2) {
  arr2.push(obj[stringName]);
  if (obj.children) {
    obj.children.forEach(childNode => {
      collectAllSystemIds(childNode, stringName, arr2);
    });
  }
  return arr2;
}

/*

 功能:判断字符串是否json格式的字符串
 str: 参数 字符串
 是 返回 true, 否 返回false
 */

export function isJsonString(str) {
  try {
    if (typeof JSON.parse(str) == "object") {
      return true;
    }
  } catch (e) {
  }
  return false;
}


// 信息提示
export function showMessage(messageService: MessageService, flag: string, message: string) {
  messageService.clear();
  messageService.add({
    severity: flag,
    summary: '信息提示框',
    detail: message || ''
  });
}


//获得某月的天数 
export function getMonthDays(myMonth) {
  const now = new Date(); //当前日期 
  let nowYear = now.getFullYear(); //当前年 
  nowYear += (nowYear < 2000) ? 1900 : 0; //
  const monthStartDate = new Date(nowYear, myMonth, 1);
  const monthEndDate = new Date(nowYear, myMonth + 1, 1);
  const days = (Number(monthEndDate) - Number(monthStartDate)) / (1000 * 60 * 60 * 24);
  return days;
}

//获得上月开始时间
export function getLastMonthStartDate() {
  const now = new Date(); //当前日期 
  let nowYear = now.getFullYear(); //当前年 
  nowYear += (nowYear < 2000) ? 1900 : 0; //
  const lastMonthDate = new Date(); //上月日期
  lastMonthDate.setDate(1);
  lastMonthDate.setMonth(lastMonthDate.getMonth() - 1);
  const lastYear = lastMonthDate.getFullYear();
  const lastMonth = lastMonthDate.getMonth();
  if (lastMonth === 11) {
    nowYear -= 1;
  }
  const lastMonthStartDate = new Date(nowYear, lastMonth, 1);
  return lastMonthStartDate;
}

//获得上月结束时间
export function getLastMonthEndDate() {
  const now = new Date(); //当前日期 
  let nowYear = now.getFullYear(); //当前年 
  nowYear += (nowYear < 2000) ? 1900 : 0; //
  const lastMonthDate = new Date(); //上月日期
  lastMonthDate.setDate(1);
  lastMonthDate.setMonth(lastMonthDate.getMonth() - 1);
  const lastYear = lastMonthDate.getFullYear();
  const lastMonth = lastMonthDate.getMonth();
  if (lastMonth === 11) {
    nowYear -= 1;
  }
  let lastMonthEndDate = new Date(nowYear, lastMonth, getMonthDays(lastMonth));
  lastMonthEndDate.setHours(23, 59, 59);
  return lastMonthEndDate;
}


