
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'mapValuePie',
    pure: true
})

export class MapValuePie implements PipeTransform {

    transform(value: any, args: any[], mapKey: string, mapVal: string): any {
        if (args) {
            for (const i in args) {
                if (value === args[i][mapKey]) {
                    return args[i][mapVal];
                }
            }
            return value;
        }
    }
}
